import { createRouter, createWebHistory } from 'vue-router';
import vCatalog from './components/v-catalog'
import vCart from './components/v-cart'
const routes = [
    { 
        path: '/',
        component: vCatalog, 
        name: 'Catalog',
        props: true 
    },
    { 
        path: '/cart',  
        component: vCart,
        name: 'Cart',
        props: true 
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes
  });

export default router